'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Badge } from '@/components/ui/badge';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { toast } from 'sonner';
import { TPAQuestion } from '@/lib/types';

export default function QuestionsPage() {
  const { user } = useAuth();
  const router = useRouter();
  const [questions, setQuestions] = useState<TPAQuestion[]>([]);
  const [filteredQuestions, setFilteredQuestions] = useState<TPAQuestion[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState('');
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [editingQuestion, setEditingQuestion] = useState<TPAQuestion | null>(null);
  const [filterCategory, setFilterCategory] = useState<string>('all');
  const [formData, setFormData] = useState({
    question: '',
    options: ['', '', '', ''],
    correctAnswer: '',
    category: 'verbal' as 'verbal' | 'numerical' | 'logical',
  });

  useEffect(() => {
    if (!user || user.role !== 'admin') {
      router.push('/login');
      return;
    }

    fetchQuestions();
  }, [user, router]);

  useEffect(() => {
    if (filterCategory === 'all') {
      setFilteredQuestions(questions);
    } else {
      setFilteredQuestions(questions.filter(q => q.category === filterCategory));
    }
  }, [questions, filterCategory]);

  const fetchQuestions = async () => {
    try {
      setIsLoading(true);
      const response = await fetch('/api/admin/questions');
      if (!response.ok) {
        throw new Error('Failed to fetch questions');
      }
      
      const result = await response.json();
      setQuestions(result.data || []);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to load questions');
    } finally {
      setIsLoading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!formData.question || formData.options.some(opt => !opt) || !formData.correctAnswer) {
      toast.error('Harap isi semua field');
      return;
    }

    if (!formData.options.includes(formData.correctAnswer)) {
      toast.error('Jawaban benar harus salah satu dari pilihan jawaban');
      return;
    }

    try {
      const url = '/api/admin/questions';
      const method = editingQuestion ? 'PUT' : 'POST';
      
      const payload = editingQuestion 
        ? { ...formData, id: editingQuestion.id }
        : formData;

      const response = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(payload),
      });

      if (!response.ok) {
        throw new Error('Failed to save question');
      }

      toast.success(editingQuestion ? 'Soal berhasil diupdate!' : 'Soal berhasil dibuat!');
      setIsDialogOpen(false);
      setEditingQuestion(null);
      resetForm();
      fetchQuestions();
    } catch (err) {
      toast.error('Gagal menyimpan soal');
    }
  };

  const handleEdit = (question: TPAQuestion) => {
    setEditingQuestion(question);
    setFormData({
      question: question.question,
      options: [...question.options],
      correctAnswer: question.correctAnswer,
      category: question.category,
    });
    setIsDialogOpen(true);
  };

  const handleDelete = async (id: string) => {
    if (!confirm('Apakah Anda yakin ingin menghapus soal ini?')) {
      return;
    }

    try {
      const response = await fetch(`/api/admin/questions?id=${id}`, {
        method: 'DELETE',
      });

      if (!response.ok) {
        throw new Error('Failed to delete question');
      }

      toast.success('Soal berhasil dihapus!');
      fetchQuestions();
    } catch (err) {
      toast.error('Gagal menghapus soal');
    }
  };

  const resetForm = () => {
    setFormData({
      question: '',
      options: ['', '', '', ''],
      correctAnswer: '',
      category: 'verbal',
    });
  };

  const openCreateDialog = () => {
    setEditingQuestion(null);
    resetForm();
    setIsDialogOpen(true);
  };

  const updateOption = (index: number, value: string) => {
    const newOptions = [...formData.options];
    newOptions[index] = value;
    setFormData(prev => ({ ...prev, options: newOptions }));
  };

  const getCategoryBadge = (category: string) => {
    const colors = {
      verbal: 'bg-blue-100 text-blue-800',
      numerical: 'bg-green-100 text-green-800',
      logical: 'bg-purple-100 text-purple-800',
    };
    
    return (
      <Badge className={colors[category as keyof typeof colors]}>
        {category === 'verbal' ? 'Verbal' : 
         category === 'numerical' ? 'Numerik' : 'Logika'}
      </Badge>
    );
  };

  if (isLoading) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="flex items-center justify-center min-h-[400px]">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto mb-4"></div>
            <p className="text-muted-foreground">Memuat soal tes...</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Header */}
      <div className="flex justify-between items-center mb-8">
        <div>
          <h1 className="text-3xl font-bold text-foreground">Manajemen Soal Tes</h1>
          <p className="text-muted-foreground">Kelola bank soal tes potensi akademik</p>
        </div>
        <div className="flex gap-4">
          <Button variant="outline" onClick={() => router.push('/admin/dashboard')}>
            Kembali ke Dashboard
          </Button>
          <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
            <DialogTrigger asChild>
              <Button onClick={openCreateDialog}>Tambah Soal</Button>
            </DialogTrigger>
            <DialogContent className="max-w-2xl">
              <DialogHeader>
                <DialogTitle>
                  {editingQuestion ? 'Edit Soal' : 'Tambah Soal Baru'}
                </DialogTitle>
                <DialogDescription>
                  {editingQuestion ? 'Update soal tes' : 'Buat soal tes baru'}
                </DialogDescription>
              </DialogHeader>
              <form onSubmit={handleSubmit} className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="category">Kategori</Label>
                  <Select 
                    value={formData.category} 
                    onValueChange={(value: 'verbal' | 'numerical' | 'logical') => 
                      setFormData(prev => ({ ...prev, category: value }))
                    }
                  >
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="verbal">Verbal</SelectItem>
                      <SelectItem value="numerical">Numerik</SelectItem>
                      <SelectItem value="logical">Logika</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="question">Pertanyaan</Label>
                  <Textarea
                    id="question"
                    value={formData.question}
                    onChange={(e) => setFormData(prev => ({ ...prev, question: e.target.value }))}
                    placeholder="Masukkan pertanyaan..."
                    rows={3}
                    required
                  />
                </div>
                
                <div className="space-y-2">
                  <Label>Pilihan Jawaban</Label>
                  {formData.options.map((option, index) => (
                    <div key={index} className="flex items-center space-x-2">
                      <span className="w-8 text-center font-medium">
                        {String.fromCharCode(65 + index)}.
                      </span>
                      <Input
                        value={option}
                        onChange={(e) => updateOption(index, e.target.value)}
                        placeholder={`Pilihan ${String.fromCharCode(65 + index)}`}
                        required
                      />
                    </div>
                  ))}
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="correctAnswer">Jawaban Benar</Label>
                  <Select 
                    value={formData.correctAnswer} 
                    onValueChange={(value) => setFormData(prev => ({ ...prev, correctAnswer: value }))}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Pilih jawaban benar" />
                    </SelectTrigger>
                    <SelectContent>
                      {formData.options.map((option, index) => (
                        option && (
                          <SelectItem key={index} value={option}>
                            {String.fromCharCode(65 + index)}. {option}
                          </SelectItem>
                        )
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                
                <div className="flex justify-end space-x-2">
                  <Button type="button" variant="outline" onClick={() => setIsDialogOpen(false)}>
                    Batal
                  </Button>
                  <Button type="submit">
                    {editingQuestion ? 'Update' : 'Simpan'}
                  </Button>
                </div>
              </form>
            </DialogContent>
          </Dialog>
        </div>
      </div>

      {error && (
        <Alert variant="destructive" className="mb-6">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      {/* Filter */}
      <Card className="mb-6">
        <CardContent className="pt-6">
          <div className="flex items-center space-x-4">
            <Label>Filter Kategori:</Label>
            <Select value={filterCategory} onValueChange={setFilterCategory}>
              <SelectTrigger className="w-48">
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">Semua Kategori</SelectItem>
                <SelectItem value="verbal">Verbal</SelectItem>
                <SelectItem value="numerical">Numerik</SelectItem>
                <SelectItem value="logical">Logika</SelectItem>
              </SelectContent>
            </Select>
            <div className="text-sm text-muted-foreground">
              Menampilkan {filteredQuestions.length} dari {questions.length} soal
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Questions Table */}
      <Card>
        <CardHeader>
          <CardTitle>Bank Soal</CardTitle>
          <CardDescription>
            Daftar semua soal tes potensi akademik
          </CardDescription>
        </CardHeader>
        <CardContent>
          {filteredQuestions.length === 0 ? (
            <div className="text-center py-8">
              <p className="text-muted-foreground">
                {filterCategory === 'all' ? 'Belum ada soal' : `Belum ada soal kategori ${filterCategory}`}
              </p>
              <Button className="mt-4" onClick={openCreateDialog}>
                Tambah Soal Pertama
              </Button>
            </div>
          ) : (
            <div className="overflow-x-auto">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>No</TableHead>
                    <TableHead>Pertanyaan</TableHead>
                    <TableHead>Kategori</TableHead>
                    <TableHead>Jawaban Benar</TableHead>
                    <TableHead>Aksi</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {filteredQuestions.map((question, index) => (
                    <TableRow key={question.id}>
                      <TableCell>{index + 1}</TableCell>
                      <TableCell className="max-w-md">
                        <div className="truncate" title={question.question}>
                          {question.question.length > 100 
                            ? `${question.question.substring(0, 100)}...` 
                            : question.question
                          }
                        </div>
                      </TableCell>
                      <TableCell>
                        {getCategoryBadge(question.category)}
                      </TableCell>
                      <TableCell className="font-medium">
                        {question.correctAnswer}
                      </TableCell>
                      <TableCell>
                        <div className="flex space-x-2">
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => handleEdit(question)}
                          >
                            Edit
                          </Button>
                          <Button
                            variant="destructive"
                            size="sm"
                            onClick={() => handleDelete(question.id)}
                          >
                            Hapus
                          </Button>
                        </div>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
